<template>
<div id="dash" :style="{marginTop:roomWidth/-4+'px',marginLeft:roomWidth/-4+'px',width:roomWidth/2+'px',height:roomWidth/2+'px'}">
  <img :id="['down_' + roomKey]" style="position:absolute;bottom:0;width:100%;z-index:-1;" :style="{bottom:roomWidth/-4+'px',marginBottom: '-10%'}" src="../../assets/traps/dash/dashdown.png" alt="">
<img style="position:absolute;bottom:0;width:100%;z-index:-1;" :style="{bottom:(roomWidth/-4)-10+'px'}" src="../../assets/traps/dash/dashforground.png" alt="">
<img :id="['top_' + roomKey]"  style="position:absolute;bottom:0;width:100%;z-index:-1;" :style="{top:(roomWidth/-4)+'px',marginTop: '-100%'}" src="../../assets/traps/dash/dashtop.png" alt="">

</div>
</template>

<script>
import Velocity from 'velocity-animate';
export default {
  props: {
    roomWidth:Number,
    roomKey: Number
  },
  data() {
    return {
    }
  },
  methods: {
    trigger() {
      var scope = this;
      $("#down_"+this.roomKey).velocity({ marginBottom: '50%' },
      {
        duration: 200,
        easing:'linear',
        delay: 800,
        complete: function() {
          $("#down_"+scope.roomKey).velocity({ marginBottom: '-10%' },
          {
            duration: 200,
            easing:'linear',
            delay: 200,
          });
        }
      })
      $("#top_"+this.roomKey).velocity({ marginTop: '0%' },
      {
        duration: 200,
        delay: 800,
        easing:'linear',
        complete: function() {
          $("#top_"+scope.roomKey).velocity({ marginTop: '-100%' },
          {
            duration: 200,
            delay: 200,
            easing:'linear',
          });
        }
      })
      
    },
    reset: function() {
      //doesn't work
      $("#down").css({marginBottom: '-10%' });
      $("#top").css({marginTop: '-100%' });
    },
    init: function() {},
  },
  mounted() {
    // this.$emit('backgroundCreated')
  }
}
</script>
<style>
  #dash{
    position: absolute;
    top: 50%;
    left: 50%;
  }
</style>



