import Vue from 'vue'
import Router from 'vue-router'
import Game from '@/components/pages/Game'
import Home from '@/components/pages/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/game',
      name: 'Game',
      component: Game
    }
  ]
})



