<template>
	<div id="viewport">
    <b-button variant="link" id="soundToggle" v-on:click="toggleSound"><icon name="volume-off"></icon></b-button>
		<background ref="background" :width="roomsWidth" :rooms="rooms"/>
		<div id="info">
			<h1>Level {{ level }}</h1>
		</div>
		<sprite ref="sprite" :spriteSize="spriteSize" />
    <div id="error" class="action hide">
        <img :src="wrongActionAsset" width="108" height="108">
    </div>
	  <div id="rooms" :style="{ width: roomsWidth + 'px', left: startOffset + 'px'}" >
			<room v-for="(room, roomKey) in rooms" :ref="('room_' + roomKey)" :roomKey="roomKey" :roomWidth="roomWidth" :type="room.actionId" :style="{ left: room.position + 'px',width:roomWidth+'px',height:roomWidth+'px',marginTop:roomWidth /-2+'px'}" :key="roomKey"/>
	  </div>
	  <div id="bottomMenu">
		  <b-button id="start" :disabled="this.inProgress" variant="link" v-on:click="startIntro" class="startbt">Démarrer</b-button>
		</div>
		<b-modal ref="modalChoices" no-close-on-esc no-close-on-backdrop hide-header-close id="modalChoice" title="Choisissez vos actions">
      <div id="actionButtons" class="text-center">
      	<b-button v-for="(action, actionKey) in actions" variant="link"
      	v-on:click="chooseAction(action.id)" class="action center-block" :key="action.id">
          <img  v-bind:src="action.asset" width="108" height="108">
        </b-button>
      </div>
      <div class="text-center">
        <div v-for="(room, roomKey) in rooms" class="dot" :id="['dot_' + roomKey]" >
        </div>
      </div>
    	<div slot="modal-footer">
         <b-button class="startbt" :disabled="this.cantTest" v-on:click="startMove" variant="link">
          OK
         </b-button>
       </div>
  	</b-modal>
		<foreground ref="foreground" :width="roomsWidth" />
		<div id="sunlight"></div>
	</div>
</template>

<script>

import etc from '@/etc';

import Velocity from 'velocity-animate';
import Lottie from '@/components/utils/Lottie.vue';
import Background from '@/components/Background.vue';
import Foreground from '@/components/Foreground.vue';
import Room from '@/components/Room.vue';
import Sprite from '@/components/Sprite.vue';

export default {
  name: 'Game',
  components: {
    'sprite':     Sprite,
    'background': Background,
		'foreground': Foreground,
    'room':       Room,
  },
  data () {
    return {
    	stringInput: '',
    	actions: [
    		{id:0, name:'Jump', anim:'jump',  
          asset:require('@/assets/ui/1x/ICO_jump.png')},
    		{id:1, name:'Dash', anim:'dash', 
          asset:require('@/assets/ui/1x/ICO_dash.png')},
    		{id:2, name:'Slide', anim:'slide', 
          asset:require('@/assets/ui/1x/ICO_slide.png')},
    		{id:3, name:'Wait', anim:'wait', 
          asset:require('@/assets/ui/1x/ICO_wait.png')},
    	],
    	levels: [],
      rooms: [],
      userInput: [],
      roomWidth: $(window).height(),
			spriteSize:$(window).height() /2.1,
      roomsWidth: 100000,
      roomCount: 0,
      inProgress: false,
      cantTest: true,
      level:1,
      startRoomNumber:2,
      totalLevel:10,
      startOffset:0,
      itvAnim: false,
      wrongActionAsset: ''
    }
  },
  watch: {
    // whenever stringInput changes, this function will run
    stringInput: function () {
      this.userInput = this.stringInput.split('');
      //handle dot light
      for (var i = 0; i < this.rooms.length; i++) {
      	if(i < this.userInput.length)
      		$('#dot_'+i).addClass('on');
      	else
      		$('#dot_'+i).removeClass('on');
      };
      //
      this.cantTest = !(this.userInput.length == this.rooms.length);
      if(this.userInput.length > this.rooms.length)
        this.stringInput = this.stringInput.substr(0, this.rooms.length);
    }
  },
  methods: {
  	reset:function() {
  		this.userInput = [];
  		this.stringInput = '';
  		this.roomCount = 0;
  		this.$refs.modalChoices.hide();
  	},
    toggleSound: function(e) {
      etc.audio.muted = !etc.audio.muted;
      $('#soundToggle').toggleClass('muted');
    },
  	win:function() {
  		if(this.level < this.totalLevel) {
  			this.level++;
  			this.rooms = this.levels[this.level - 1];
  			this.defineRoomPosition();
  			this.startOffset = -this.rooms[this.rooms.length - 1].position;
  			this.$refs.sprite.hide();
  			this.$refs.modalChoices.hide();
  		}
  	},
  	chooseAction: function(actionId) {
  		this.stringInput += ''+actionId;
  	},
    setSpriteAnim: function (state) {
      clearTimeout(this.itvAnim);
      this.$refs.sprite.setSpriteAnim(state);
    },
  	init: function() {
			this.roomWidth = $(window).height();
  		var scope = this;
  		this.rooms = this.levels[this.level - 1];
  		this.defineRoomPosition();
  		this.startOffset = -this.rooms[this.rooms.length - 1].position;
  	},
  	startIntro: function() {
  		this.$refs.sprite.hide();
      $('#error').addClass('hide');
  		if(this.userInput) {
  			this.reset();
  		}
  		var scope = this;
  		this.inProgress = true;
  		$('#start').css({'display': 'none'}, {'background-position': '0 0'});
  		$("#rooms").velocity({ left: this.roomWidth+'px' },
  		{
  			duration: this.rooms.length*.5*2000,
        easing:'easeInOutSine',
        complete: function() {
        	scope.startTyping();
        },
				progress: function(elements, complete, remaining, start, tweenValue) {

				scope.backgroundParallax();
    }
  		});
  	},
		backgroundParallax:function(){
			this.$refs.background.slide($("#rooms").css('left').replace(/[^-\d\.]/g, ''));
			this.$refs.foreground.slide($("#rooms").css('left').replace(/[^-\d\.]/g, ''));
		},
  	startTyping: function() {
  		this.$refs.modalChoices.show();
  		this.$refs.sprite.show();
      this.setSpriteAnim('static');
  		this.inProgress = false;
  	},
  	defineRoomPosition: function() {
  		var totalPosition = 0;
  		for (var i = 0; i < this.rooms.length; i++) {
  			totalPosition += this.rooms[i].gap;
  			this.rooms[i].position = totalPosition;
  			this.rooms[i].id = i;
  			totalPosition += this.roomWidth;
  		};
  		//update rooms width
  		this.roomsWidth = totalPosition+1000;
  	},
  	startMove: function() {
  		this.$refs.modalChoices.hide();
  		var scope = this;
  		this.inProgress = this.cantTest = true;
			this.move();
			this.setSpriteAnim('run');
  	},
  	move: function() {

  		if(this.roomCount < this.rooms.length)
  		{
  			this.moveToRoom(this.rooms[this.roomCount]);
  			this.roomCount++;
  		}
  		else {
        var scope = this;
        this.setSpriteAnim('run');
        var offset = this.rooms[this.rooms.length - 1].position + this.roomWidth;
  			//end session
        $("#rooms").velocity({ left: -offset+'px' },
          {
            duration: 2000,
            complete: function() {
              scope.setSpriteAnim('static');
              setTimeout(function() {
                scope.endMove();
                scope.win();
              }, 1000);
            },
            progress: function(elements, complete, remaining, start, tweenValue) {
              scope.backgroundParallax();
            },
            easing:'linear',
        });

  		}
  	},
  	moveToRoom:function(room) {
  		var scope = this;
      //var isCheck = false;
  		//to do: resolve issue with scroll hidden
  		var offset = room.position;
  		$("#rooms").velocity({ left: -offset+'px' },
  		{
  			duration: 2000 + room.gap,
        complete: function() {
        	scope.checkRoom(room);
        },
				progress: function(elements, complete, remaining, start, tweenValue) {
					scope.backgroundParallax();
          //check if sprite enter the room
          /*var currentLeft = parseFloat($("#rooms").css("left"));
          //hit position: final destination - sprite width
          var hitPosition = (offset - 400 )
          if(currentLeft < -hitPosition && !isCheck) {
            scope.checkRoom(room);
            isCheck = true;
          }*/
        },
        easing:'linear',
  		});
  		//method with scroll
			/*$("#rooms").velocity('scroll', {
			 	offset: offset+'px',
  			duration: 2000+room.gap,
        complete: function() {
        	scope.checkRoom(room);
        },
        container:$('#viewport'),
        easing:'linear',
        axis: "x"
  		});*/
  	},
  	checkRoom: function(room) {
  		var scope = this;
      /*** IF GOOD ACTION ***/
  		if(room.actionId == this.userInput[room.id]) {
  			$('#room_'+room.id).addClass('ok');
  			$('#input_'+room.id).addClass('ok');
        if(room.anim != 'wait')
  			 this.move();
        this.setSpriteAnim(room.anim);

				this.$refs['room_'+room.id][0].triggerAnim(room.actionId);
        //this.$refs['room_${room.id]}'][0].triggerAnim(room.actionId);
        //console.log(this.$refs['room_'+room.id]);
        scope.itvAnim = setTimeout(function() {
          scope.setSpriteAnim('run');
          if(room.anim == 'wait')
            scope.move();
        }, 1000);
  		}
      /*** IF WRONG ACTION ***/
  		else {
        this.wrongActionAsset = this.actions[room.actionId].asset;
        $('#error').removeClass('hide');
        scope.setSpriteAnim('static');
  			scope.endMove();
  		}
  		/*setTimeout(function() {
  			scope.move();
  		}, 1000);*/

  	},
  	endMove: function() {
  		this.inProgress = false;
  		$('#start').css({'display': 'block'});
  	},
  	generateLevels: function() {
  		for (var i = 1; i < this.totalLevel + 1; i++) {
  			var level = [];
  			for (var j = 0; j < this.startRoomNumber; j++) {
  				var actionId = Math.round(Math.random() * (this.actions.length - 1));
          //var gap = Math.round(Math.random() * 300);
  				var gap = this.roomWidth;
  				var room = {actionId: actionId, gap: gap, anim:this.actions[actionId].anim};
  				level.push(room);
  			};
  			this.levels.push(level);
  			this.startRoomNumber++;
  		};
  	}
  },
  created: function() {
  	this.generateLevels();
  	this.init();
  },
  mounted: function() {
    if(!etc.audio)
      $("#soundToggle").addClass("hide");
  }
}
</script>

<style lang="scss">

  $asset_path:'../../assets/';

  button.startbt {
    background: transparent url($asset_path + 'ui/1x/ICO_ext_play_undo.png') no-repeat top left!important;
    background-size: 216px 108px!important;
    width: 108px;
    height: 108px;
    border: 0;
    border-radius: 0;
    text-indent: -9999px;
    overflow: hidden;
    outline:none; box-shadow:none;
  }
  button.startbt.retry {
    background-position: -108px 0px;
  }
</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

	#viewport {
		position: absolute;
		top: 0; left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: #52bf74;
		z-index: -9;
	}

	#info {
		position: absolute;
		top: 10px;
		right: 10px;
		width: 100%;
		h1 {
      font-size: 16px;
      width: 100%;
      text-align: right;
		}
	}

	#roomsContainer {
		position: absolute;
		top: 0; left: 0;
		width: 100%;
		height: 100%;
	}

	#rooms {
		top: 50%;
		// margin-top: -200px;
	}

	#rooms {
		position: absolute;
		// left: $roomWidth;
		margin-right: 1000px;
	}

	#start {
		position: fixed;
		bottom: 40px;
		left: 50%;
		transform: translateX(-50%);
	}

	.guy {
		position: relative;
		height: 60%;
		width: 10%;
		margin-top: 40%;
		background: black;
		margin-left: 20px;
	}

	.room {
		position: absolute;
		p {
			font-size: 30px;
			text-align: center;
			width: 100%;
		}
	}

	#sunlight{
		position: absolute;
		top:0;
		bottom:0;
		left:0;
		right:0;
		z-index: -1;
		background-image: linear-gradient(to left bottom, rgba(#e8e8e8, .45), rgba(#7ABF8F, 0));
	}

	.dot {
		width: 10px;
		height: 10px;
		background: #FFFDC9;
		border-radius: 10px;
		display: inline-block;
		margin-right: 2px;
	}

	.dot.on {
		background: #452C00;
	}

	#modalChoice button {
		margin-right: 8px;
    margin-bottom: 8px;
	}

  .action {
    border-radius: 10px;
    background: #FFFDC9;
    border: 4px solid #452C00;
    height: 108px;
    width: 108px;
    margin-top: 0;
    box-shadow: 0 4px 0 0px #603700;
    transition: all .2s;
    padding: 0;
    img {
      max-width: 100%;
      height: auto;
    }
    position: relative;
    top:0;
  }

  #error {
    border-color: red;
    
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 0 0 0 0;
    position: absolute;
  }

  #actionButtons {
    height: 120px;
    position: relative;
  }

  button.action:hover {
    
    top: 4px;
    box-shadow: 0 2px 0 0px #603700;
    transform: scale(0.99)
  }

  #soundToggle {
    color: #603700;
    z-index: 9999;
    position: absolute;
    top: 0;
    left: 0;
  }

  #soundToggle.muted {
    opacity: .5;
  }

</style>



