<template>
<div class="room" :id="['room_' + roomKey]" >
  <jump :roomWidth="roomWidth" v-if="type===0" ref='Room0'></jump>
  <dash :roomKey="roomKey" :roomWidth="roomWidth*1.2" v-if="type===1" ref='Room1'></dash>
  <slide :roomWidth="roomWidth" v-if="type===2" ref='Room2'></Slide>
  <wait :roomWidth="roomWidth" v-if="type===3" ref='Room3'></Wait>
</div>
</template>

<script>
import Dash from './rooms/Dash.vue';
import Slide from './rooms/Slide.vue';
import Jump from './rooms/Jump.vue';
import Wait from './rooms/Wait.vue';
export default {
  name: 'Room',
  components: {
    'dash': Dash,
    'slide': Slide,
    'jump': Jump,
    'wait': Wait,
  },
  props: {
    id: 'room',
    type: Number,
    roomKey: Number,
    roomWidth:Number
  },
  data() {
    return {
    }
  },
  methods: {
    triggerAnim: function(type) {
      this.$refs['Room'+type].trigger();
      //console.log('triggerAnim');
    },
    resetAnim: function(type) {
      
    },
    init: function() {

    },
  },
  mounted() {

  }
}
</script>
<style lang="scss" scoped>

	$roomWidth: 400px;
  .room {

		// width: $roomWidth;
		// height: $roomWidth;
		// background: #EEE;
		// border: 1px solid #000;
		position: absolute;
		p {
			font-size: 30px;
			text-align: center;
			width: 100%;
		}
	}
</style>



