<template>
  <div id="sprite" :style="{marginTop:spriteSize /-2+'px'}">
    <lottie id="runAnim" :options="runAnimOptions" :height="spriteSize" :width="spriteSize" v-on:animCreated="handleRunAnimation" class="animatedSprite" />
    <lottie id="staticAnim" :options="staticAnimOptions" :height="spriteSize" :width="spriteSize" v-on:animCreated="handleStaticAnimation" class="animatedSprite"  />
    <lottie id="slideAnim" :options="slideAnimOptions" :height="spriteSize" :width="spriteSize" v-on:animCreated="handleSlideAnimation" class="animatedSprite" />
    <lottie id="dashAnim" :options="dashAnimOptions" :height="spriteSize" :width="spriteSize" v-on:animCreated="handleDashAnimation" class="animatedSprite"  />
    <lottie id="waitAnim" :options="waitAnimOptions" :height="spriteSize" :width="spriteSize" v-on:animCreated="handleWaitAnimation" class="animatedSprite"  />
    <lottie id="jumpAnim" :options="jumpAnimOptions" :height="spriteSize" :width="spriteSize" v-on:animCreated="handleJumpAnimation" class="animatedSprite"  />
  </div>
</template>

<!-- SCRIPT -->
<script>
import Lottie from '@/components/utils/Lottie.vue';

import * as runAnim from '@/assets/anim/run.json';
import * as staticAnim from '@/assets/anim/static.json';
import * as slideAnim from '@/assets/anim/slide.json';
import * as waitAnim from '@/assets/anim/wait.json';
import * as dashAnim from '@/assets/anim/dash.json';
import * as jumpAnim from '@/assets/anim/jump.json';

export default {
  name: 'Sprite',
  props: {
  	spriteSize: Number
  },
  components: {
    'lottie':     Lottie,
  },
  data () {
    return {
      runAnimOptions:     { animationData: runAnim, autoplay: false },
      staticAnimOptions:  { animationData: staticAnim, autoplay: false },
      dashAnimOptions:    { animationData: dashAnim, autoplay: false },
      waitAnimOptions:    { animationData: waitAnim, autoplay: false },
      slideAnimOptions:   { animationData: slideAnim, autoplay: false },
      jumpAnimOptions:    { animationData: jumpAnim, autoplay: false },
    }
  },
  methods: {
    handleRunAnimation: function (anim) {
      this.runAnim = anim;
    },
    handleStaticAnimation: function (anim) {
      this.staticAnim = anim;
    },
    handleWaitAnimation: function (anim) {
      this.waitAnim = anim;
    },
    handleSlideAnimation: function (anim) {
      this.slideAnim = anim;
    },
    handleDashAnimation: function (anim) {
      this.dashAnim = anim;
    },
    handleJumpAnimation: function (anim) {
      this.jumpAnim = anim;
    },
    setSpriteAnim: function (state) {
      var anim = ['run', 'static', 'wait', 'dash', 'slide', 'jump'];
      for (var i = 0; i < anim.length; i++) {
        if(anim[i] == state) {
          this[anim[i]+'Anim'].play();
          $('#'+anim[i]+'Anim').removeClass('hide');
        }
        else {
          this[anim[i]+'Anim'].pause();
          $('#'+anim[i]+'Anim').addClass('hide');
        }
      };
    },
    show: function() {
    	$('#sprite').addClass('show');
    },
    hide: function() {
    	$('#sprite').removeClass('show');
    }
  }
}
</script>

<!-- SCOPED CSS -->
<style lang="scss" scoped>
  $spriteWidth: 400px;
  #sprite {
    position: absolute;
    top:50%;
    left: 0;
    z-index: 100;
    //border: 1px solid red;
    width: $spriteWidth;
    height: $spriteWidth;
    opacity: 0;
    transition: opacity .5s ease-in-out;
    z-index: -1;
    .animatedSprite {
      position: absolute;
    }
  }
  #sprite.show {
		opacity: 1;
	}
</style>




